import {mapMutations} from 'vuex';
import request from '../../utils/request.js';
export default {
	data() {
		return {
			good:{}
		}
	},
	async mounted(){
		// console.log(this.$mp)
		let {goodId} = this.$mp.query;
		let result = await request('/getGoodDetail',{goodId});
		console.log(result)
		this.good=result.data;
	},
	methods:{
		addCart(){
			this.addShopItemMutation(this.good)
		},
		...mapMutations(["addShopItemMutation"])
	}
}
